$black: #000000;
$white: #ffffff;
$grey:  #808080;

* {
  font-family: "Montserrat", sans-serif;
}

.menuContainer {
  overflow: hidden;
  background-color: $white;
  margin-bottom: 30px;
  & a {
    float: left;
    display: block;
    color: $black;
    text-align: center;
    padding: 20px 15px;
    text-decoration: none;
    font-size: 17px;
  }
  & a.logo:hover {
    color: $white;
    border: 1px solid $black;
  }
  & .menuContainerRight {
    float: right;
    text-decoration: none;
  }

  & a:hover {
    background-color: $black;
    color: $white;
    border: 1px solid $black;
  }
  & .burguer {
    display: none;
  }
  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    width: 80%;
    left: calc(10% - 5px);
    right: 50%;
    border-top: 2px solid $grey;
    margin-top:60px;
}
}

  @media screen and (max-width: 720px) {
    .menuContainer a:not(:first-child) {
      display: none;
    }
    .menuContainer a.burguer {
      float: right;
      display: block;
    }
  }

  @media screen and (max-width: 720px) {
    .menuContainer.responsive {
      position: relative;
    }
    .menuContainer.responsive .burguer {
      position: absolute;
      right: 0;
      top: 0;
    }
    .menuContainer.responsive a {
      float: none;
      display: block;
      text-align: left;
    }
  }